.page-header {
  margin: 0 0 120px;

  &--catalog {
    margin: 0 0 80px;

    .page-header__image-block-text {
      p {
        font-size: 34px;
        line-height: 44px;
        letter-spacing: 0;
      }

      span {
        font-weight: 500;
        font-size: 22px;
        line-height: 29px;
      }
    }

    .page-header__image-block  {
      height: 261px;
      background-image: url('../img/catalog-main.jpg');

      @media (min-resolution: 2dppx) {
        background-image: url('../img/catalog-main@2x.jpg');
      }
    }
  }
}

.page-header__header {
  position: relative;
  max-width: 1366px;
  margin: auto;
  padding: 30px 100px 0;
}

.page-header__logo {
  position: absolute;
  left: 50%;
  transform:  translate(-50%,0);
  z-index: 10;

  svg {
    fill: $accent-color;
  }

  a {
    &:focus {
      outline: 1px dashed $accent-color;
    }

    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.page-header__nav-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.page-header__burger {
  display: none;
}

.page-header__search-field-wrapper {
  box-sizing: border-box;
  position: relative;
  display: flex;
  align-items: center;
  flex-basis: 50%;
  padding: 10px 70px 10px 0;

  input {
    @include interactiveFocusEffect;
    padding: 0 0 0 25px;
    border: 0;
    font-family: "Poppins";
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    color: $additional-color;

    &::placeholder {
      color: $additional-color;
    }
  }

  svg {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 14px;
    height: 14px;
    margin: auto;
    fill: $additional-color-two;
  }

  &:focus {
    outline: 1px dashed $accent-color;
  }

  &:hover {
    input {
      &::placeholder {
        color: $accent-color;
      }
    }

    svg {
      fill: $accent-color;
    }
  }

  &:active {
    opacity: 0.7;
  }
}

.page-header__user-nav-wrapper {
  @include resetListDefaultProperties;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  flex-basis: 50%;
  padding: 10px 0 10px 70px;

  a {
    &:focus {
      outline: 1px dashed $accent-color;
    }

    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.page-header__login-button {
  margin: 0 25px 0;

  a {
    font-family: "Poppins";
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
    color: $primary-color;
  }
}

.page-header__cart {
  a {
    font-family: "Poppins";
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
    color: $primary-color;
  }

  svg {
    display: none;
  }
}

.page-header__site-nav-wrapper {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  padding: 59px 20px 17px;

  a {
    &:focus {
      outline: 1px dashed $accent-color;
    }

    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.page-header__site-nav-list {
  @include resetListDefaultProperties;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 0 0 10px;

  li {
    margin: 0 18px;
  }

  a {
    font-family: "Poppins";
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: $primary-color;
  }
}

.page-header__additional-nav-wrapper {
  display: none;
}

.page-header__image-block {
  position: relative;
  max-width: 1366px;
  height: 463px;
  margin: auto;
  background: url('../img/main.jpg') center no-repeat rgba($additional-color-five, 0.7);
  background-size: cover;
  z-index: 1;

  @media (min-resolution: 2dppx) {
    background-image: url('../img/main@2x.jpg');
  }
}

.page-header__image-block-darkening {
  width: 100%;
  height: 100%;
  background-color: rgba($additional-color-five, 0.7);
  z-index: 2;
}

.page-header__image-block-text {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  padding: 0 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  p {
    margin: 0;
    font-family: "Cormorant Garamond";
    font-size: 45px;
    font-weight: 700;
    line-height: 58px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $secondary-color;
  }

  span {
    font-family: "Cormorant Garamond";
    font-size: 38px;
    font-style: italic;
    font-weight: 400;
    line-height: 49px;
    letter-spacing: -0.04em;
    text-align: center;
    color: $secondary-color;
  }
}

@media (max-width: $desktop-width) {
  .page-header {
    margin: 0 0 70px;

    &--catalog {
      margin: 0 0 50px;

      .page-header__image-block  {
        background-image: url('../img/catalog-main-tablet.jpg');

        @media (min-resolution: 2dppx) {
          background-image: url('../img/catalog-main-tablet@2x.jpg');
        }
      }

      .page-header__image-block-text {
        margin: 0 0 5px;

        p {
          margin: 0 0 5px 0px;
        }

        span {
          margin: 0;
        }
      }
    }
  }

  .page-header__header {
    padding: 30px 45px 50px;
    background: $accent-color;
  }

  .page-header__logo {
    svg {
      fill: $secondary-color;
    }

    a {
      &:focus {
        outline: 1px dashed $secondary-color;
      }
    }
  }

  .page-header__nav-wrapper {
    flex-direction: column;
    padding: 25px 0;
  }

  .page-header__burger {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 16px;
    margin: 0 0 65px;
    padding: 0;
    background: transparent;
    border: 0;

    &::before {
      content: "";
      width: 25px;
      height: 2px;
      background: $secondary-color;
      box-shadow:
        0 8px 0 0 $secondary-color,
        0 -8px 0 0 $secondary-color;
    }


    &:active {
      &::before {
        background: $accent-color;
        box-shadow:
          0 6px 0 0 $accent-color,
          0 -6px 0 0 $accent-color;
      }
    }
  }

  .page-header__search-field-wrapper {
    margin: 0 0 45px;
    padding: 0;

    input {
      width: 100%;
      height: 40px;
      padding: 0 20px 0 40px;
      background: rgba($secondary-color, 0.15);
      border-radius: 21px;
      color: $secondary-color;

      &::placeholder {
        color: transparent;
      }
    }

    svg {
      left: 12px;
      width: 16px;
      height: 16px;
      fill: $secondary-color;
    }
  }

  .page-header__user-nav-wrapper {
    justify-content: flex-start;
    padding: 0;
    order: 1;
  }

  .page-header__login-button {
    margin: 0;

    a {
      font-size: 18px;
      line-height: 23px;
      color: $secondary-color;
    }
  }

  .page-header__cart {
    position: absolute;
    right: 44px;
    top: 55px;
    display: flex;
    align-items: center;

    span {
      @include visually-hidden
    }

    svg {
      display: block;
      stroke: $secondary-color;
    }
  }

  .page-header__site-nav-wrapper {
    justify-content: flex-start;
    margin: 0 0 20px;
    padding: 0;
  }

  .page-header__site-nav-list {
    flex-direction: column;
    margin: 0;

    li {
      margin: 0 0 21px;
    }

    a {
      font-size: 24px;
      line-height: 36px;
      color: $secondary-color;
    }
  }

  .page-header__additional-nav-wrapper {
    display: flex;
  }

  .page-header__additional-nav-list {
    @include resetListDefaultProperties;
    display: flex;
    flex-direction: column;
    margin: 0 0 20px;

    li {
      margin: 0 0 19px;
    }

    a {
      font-family: "Poppins";
      font-size: 18px;
      font-weight: 500;
      line-height: 23px;
      color: $secondary-color;
    }
  }

  .page-header__image-block {
    height: 463px;
    background-image: url('../img/main-tablet.jpg');

    @media (min-resolution: 2dppx) {
      background-image: url('../img/main-tablet@2x.jpg');
    }
  }

  .page-header__image-block-text {
    margin: 5px 15px 0 0;

    span {
      margin: -7px 10px 7px 0;
    }
  }
}

@media (max-width: $tablet-width) {
  .page-header {
    margin: 0 0 60px;

    &--catalog {
      margin: 0 0 40px;

      .page-header__image-block  {
        background-image: url('../img/catalog-main-mobile.jpg');

        @media (min-resolution: 2dppx) {
          background-image: url('../img/catalog-main-mobile@2x.jpg');
        }
      }

      .page-header__image-block-text {
        margin: 0;

        p {
          margin: 0;
        }
      }
    }
  }

  .page-header__header {
    padding: 20px 15px 25px;
  }

  .page-header__logo {
    svg {
      width: 56px;
      height: 52px;
    }
  }

  .page-header__nav-wrapper {
    padding: 18px 0;
  }

  .page-header__cart {
    top: 37px;
    right: 14px;
  }

  .page-header__burger {
    margin: 0 0 47px;
  }

  .page-header__search-field-wrapper {
    margin: 0 0 38px;
  }

  .page-header__site-nav-wrapper {
    margin: 0 0 9px;
  }

  .page-header__additional-nav-list {
    margin: 0 0 10px;
  }

  .page-header__image-block {
    height: 463px;
    background-image: url('../img/main-mobile.jpg');

    @media (min-resolution: 2dppx) {
      background-image: url('../img/main-mobile@2x.jpg');
    }
  }

  .page-header__image-block-text {
    margin: 0;

    p {
      margin: 5px 0 0 0;
      font-size: 38px;
      line-height: 49px;
    }

    span {
      font-size: 34px;
      line-height: 44px;
    }
  }
}
